















import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";

type Tab = [{ tab: string }, { tab: string }];

@Component
export default class Tabs extends Vue {
  @Inject() eventBus!: Vue;
  selectedTab = "";
  tabs: Tab = [{ tab: "" }, { tab: "" }];

  //
  mounted() {
    this.eventBus.$emit("updateSelectedTab", this.selectedTab); // Ico.vue
  }

  //
  changeSelectedTab(tab: string) {
    this.selectedTab = tab;
    this.eventBus.$emit("updateSelectedTab", this.selectedTab); // updateSelectedTabIco.vue
    // selectedTabOnce.vueselectedTab
    this.$emit("selectedTab", this.selectedTab); // tap
  }
}
