































import Vue from "vue";
import { Component, Inject, Prop } from "vue-property-decorator";

@Component
export default class Keyboard extends Vue {
  @Inject() eventBus!: Vue;
  @Prop() keyboardIconArr!: [{}]; // 
  @Prop() buttonArr!: [{}]; // 

  output = "";
  offkeyboard = false;

  created() {
    this.eventBus.$on("initOutput", (output: string) => {
      // initOutputMessage.vueinitOutput
      this.output = output;
    });
  }

  // 
  inputContent(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!.trim();
    if (this.output.length === 8) {
      return;
    }
    if (this.output === "0") {
      if ("0123456789".indexOf(input) >= 0) {
        this.output = input;
        this.updateOutput();
      } else {
        this.output += input;
        this.updateOutput();
      }
      return;
    }
    if (this.output.indexOf(".") >= 0 && input === ".") {
      return;
    }
    this.output += input;
    this.updateOutput();
  }

  // name
  matching(name: string) {
    if (name === "backspace") {
      this.remove();
    }
    if (name === "empty") {
      this.clear();
    }
    if (name === "true") {
      this.ok();
    }
  }
  mounted() {
    this.eventBus.$on("reset", () => {
      this.output = "";
    });
  }

  // 
  remove() {
    if (this.output.length === 1) {
      this.output = "0";
      this.updateOutput();
    } else {
      this.output = this.output.slice(0, -1);
      this.updateOutput();
    }
  }

  // 
  clear() {
    this.output = "0";
    this.updateOutput();
  }

  // ok
  ok() {
    this.updateOutput();
    this.offKeyboard();
    this.$emit("update");
    this.output = "";
    this.updateOutput();
  }

  updateOutput() {
    this.eventBus.$emit("updateOutput", this.output); // updateOutputOnce.vue
  }

  offKeyboard() {
    this.$emit("updateOffKeyboard", this.offkeyboard); // Once.vue
  }
}
