// 
const chineseMonth = (month: number) => {
    let chineseMonth;

    chineseMonthList.map(() => {
        chineseMonth = chineseMonthList[month];
    });
    return chineseMonth;
};
export default {
    getNewDate,
    getCurrentTime,
    getDate,
    chineseMonth,
    week,
};