






import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Echarts from "@/components/Statement/echarts.vue";

@Component({
  components: { Echarts },
})
export default class IcoRecordList extends Vue {
  @Prop() data!: {
    icoRecordList: any;
  };
  get Options() {
    return {
      tooltip: {
        //
        trigger: "item",
        formatter: "{a} <br/>{b} : {c} ({d}%)", // 
      },
      color: ["#9fd26a", "#ea5e69", "#a4789f"],
      series: [
        {
          name: "",
          type: "pie",
          radius: "70%",
          center: ["45%", "45%"],
          data: [
            { value: this.data.icoRecordList[1], name: "" },
            { value: this.data.icoRecordList[2], name: "" },
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
          },
        },
      ],
    };
  }
}
