










import Vue from "vue";
import { Component, Inject } from "vue-property-decorator";
import Echarts from "@/components/Statement/echarts.vue";

@Component({
  components: { Echarts },
})
export default class Statistics extends Vue {
  @Inject() eventBus!: Vue;

  created() {
    this.eventBus.$emit("updateBalance", this.balance);
  }
  // 
  get balance() {
    return this.earningTotal - this.payTotal;
  }
  // storerecordList
  get recordList() {
    return this.$store.state.recordList;
  }

  // 
  get pay() {
    return this.recordList
      .filter((i: { selectedTag: string }) => i.selectedTag === "")
      .sort((a: any, b: any) => {
        return (
          new Date(b.selectedDate).getTime() -
          new Date(a.selectedDate).getTime()
        );
      });
  }

  // 
  get payTotal() {
    return this.pay.reduce((sum: number, item: { output: string }) => {
      return sum + Number(item.output);
    }, 0);
  }

  // 
  get earning() {
    return this.recordList
      .filter((i: { selectedTag: string }) => i.selectedTag === "")
      .sort((a: any, b: any) => {
        return (
          new Date(b.selectedDate).getTime() -
          new Date(a.selectedDate).getTime()
        );
      });
  }

  // 
  get earningTotal() {
    return this.earning.reduce((sum: number, item: { output: string }) => {
      return sum + Number(item.output);
    }, 0);
  }

  // echart
  get totalOptions() {
    return {
      dataset: {
        source: [
          ["sum", "type"],
          [this.earningTotal, ""],
          [this.payTotal, ""],
          [this.earningTotal - this.payTotal, ""],
        ],
      },
      grid: {
        top: "15%", // grid 
        bottom: "10%",
        left: "22%",
        right: "3%",
        width: "66%",
      },
      xAxis: {
        //  grid  x 
        name: "",
        show: false, //  x 
        splitLine: { show: false }, //  grid 
        axisLabel: {
          // 
          color: "#999",
        },
        axisTick: {
          //x
          show: false, // 
        },
        axisLine: {
          //x
          show: false, // 
        },
      },
      yAxis: {
        type: "category",
        //maxInterval: 300,
        splitLine: { show: false },
        fontSize: "35",
        axisLabel: {
          margin: 12,
          textStyle: {
            fontSize: 12, //
          },
          color: function (params: any, index: any) {
            const colorList = ["#9fd26a", "#ea5e69", "#a4789f"];
            return colorList[index];
          },
        },
        axisTick: {
          //y
          show: false,
        },
        axisLine: {
          //y
          show: false,
        },
      },
      series: [
        {
          type: "bar", // 
          barWidth: 7,
          encode: {
            //  data 
            // Map the "sum" column to X axis.
            x: "sum",
            // Map the "type" column to Y axis
            y: "type",
          },

          itemStyle: {
            // 
            normal: {
              label: {
                formatter: "" + "{@sum}" + "",
                show: true,
                position: "right",
                textStyle: {
                  fontWeight: "bolder",
                  fontSize: "8",
                  color: "#fff",
                },
              },
              /*
                                
                                color:function(d){
                                    return "#"+Math.floor(Math.random()*(256*256*256-1)).toString(16);
                                }*/

              /**/
              color: function (params: any) {
                const colorList = ["#9fd26a", "#ea5e69", "#a4789f"];
                return colorList[params.dataIndex];
              },
            },
          },
        },
      ],
    };
  }
}
